.app {
  font-family: Arial, sans-serif;
  text-align: center;
  padding: 20px;
}

.exercise-select {
  width: 300px;
  margin: 20px;
}

.start-program-button, .skip-rest-button, .next-set-button {
  background-color: #4CAF50;
  color: white;
  padding: 10px;
  margin: 10px;
  border: none;
  cursor: pointer;
}

.start-program-button:hover, .skip-rest-button:hover, .next-set-button:hover {
  background-color: #45a049;
}

.completion-message {
  color: green;
  font-size: 1.5rem;
  margin-top: 20px;
}

button {
  font-size: 16px;
  border-radius: 5px;
}

button:active {
  transform: scale(0.98);
}

button:hover {
  cursor: pointer;
}

h1 {
  font-size: 2rem;
}

